.navbar {
    display: flex;
    flex-direction: row;
    background-color: #1D1010;
    color: white;
    align-items: center;
    justify-content: space-between;
    padding: 30px 45px;
    font-size: 21px;
}

.email {
    text-decoration: none;
    color: white;
    font-weight: 500;
    transition: all 0.4s ease;
}

.nav-pages {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    gap: 45px;
    list-style-type: none;
    padding: 0px;
    margin: 0px;
    font-weight: 600;
    cursor: pointer;
}

.nav-link {
    transition: all 0.4s ease;
}

.email:hover,
.nav-link:hover {
    color: rgb(235, 81, 54);
}



@media screen and (max-width: 480px) {
    .navbar {
        padding: 25px 15px;
        font-size: 12px;
        justify-content: center;
        gap: 30px;
    }

    .nav-pages {
        gap: 30px;
        font-size: 17px;
    }

    .email {
        display: none;
    }
}